import React from 'react';
import { Container } from 'react-bootstrap';
import { Card, CardContent, CardMedia, Typography } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

import { HeaderImageUrl, events } from '../special/Values';
import HeaderImage from '../components/HeaderImage';

// Import CSS
import '../css/page.css';

function Events() {
    return (
        <div>
            <Helmet>
                <title>Events | Grandis Library</title>
            </Helmet>
            <HeaderImage imageUrl={`${HeaderImageUrl.fox}.jpg`}/>
            <h1 className="page-title">Events</h1>
            <h2 className="page-subtitle">Training Related</h2>
            <Container>
                    {
                        events.training.map(el => 
                                <Card className="content-card">
                                    <Link to={`events/${el[0]}`}><CardMedia className="content-card-image" image={`${process.env.PUBLIC_URL}/thumbnail/${el[0]}.png`} title={el[1]}/></Link>
                                    <CardContent>
                                        <Typography gutterBottom variant="h5" component="h2">
                                            <Link to={`content/${el[0]}`}>{el[1]}</Link>
                                        </Typography>
                                        <div class="card-text-container">
                                            <Typography className="typo-text" color="textPrimary" variant="body2" component="p">{el[2]}</Typography>
                                        </div>
                                    </CardContent>
                                </Card>
                        )
                    }
            </Container>
            <h2 className="page-subtitle">Item Related</h2>
            <Container>
                    {
                        events.item.map(el => 
                                <Card className="content-card">
                                    <Link to={`events/${el[0]}`}><CardMedia className="content-card-image" image={`${process.env.PUBLIC_URL}/thumbnail/${el[0]}.png`} title={el[1]}/></Link>
                                    <CardContent>
                                        <Typography gutterBottom variant="h5" component="h2">
                                            <Link to={`content/${el[0]}`}>{el[1]}</Link>
                                        </Typography>
                                        <div class="card-text-container">
                                            <Typography className="typo-text" color="textPrimary" variant="body2" component="p">{el[2]}</Typography>
                                        </div>
                                    </CardContent>
                                </Card>
                        )
                    }
            </Container>
            <h2 className="page-subtitle">Update Related</h2>
            <Container>
                    {
                        events.update.map(el => 
                                <Card className="content-card">
                                    <Link to={`events/${el[0]}`}><CardMedia className="content-card-image" image={`${process.env.PUBLIC_URL}/thumbnail/${el[0]}.png`} title={el[1]}/></Link>
                                    <CardContent>
                                        <Typography gutterBottom variant="h5" component="h2">
                                            <Link to={`content/${el[0]}`}>{el[1]}</Link>
                                        </Typography>
                                        <div class="card-text-container">
                                            <Typography className="typo-text" color="textPrimary" variant="body2" component="p">{el[2]}</Typography>
                                        </div>
                                    </CardContent>
                                </Card>
                        )
                    }
            </Container>
        </div>
    )
}

export default Events
